import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, Grid, Typography } from '@mui/material';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Plan from './components/Plan';
import UserStats from './components/UserStats';
import ContractStats from './components/ContractStats';
import { AbiItem } from 'ethereum-abi-types-generator';

import ConnectButton from "./components/ConnectButton";

import bnbfarm from "./assets/img/bnbfarm-glow.png";
import bnbfarmtext from "./assets/img/bnbfarm-header-text.png";

import {ContractContext} from './assets/abi/farm-contract';
import Web3 from "web3";
import copy from 'clipboard-copy';

//
//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/farm-contract.json";
import { Opacity } from '@mui/icons-material';
//const contractAddress: string = '0x0f6EBFF0d3Abc8E061656E5a34F8d70aaf687Cba'; // testnet
//const contractAddress: string = '0x38B655c43518ecA919B75d8AC6BF8771858dFD5E'; // testnet
//const contractAddress: string = '0x13f1E7cb0b2e1e0A2189CB23f9aDbFECB70b5b63'; // testnet
//const contractAddress: string = '0x5dcE1204509689830F20f6300bFfdCB1814BDdbe'; // testnet
const contractAddress: string = '0x7D71359Ab41EE8d00d495b4bFaAa22a3b244c244'; // release on Mainnet


declare global {
  interface Window {
    farm_contract: ContractContext;
    web3: Web3;
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[]
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
  }
}

interface AppProps {
  style?: React.CSSProperties;
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 0.95
};


class App extends React.Component<AppProps> {
  
  state = {
    modalOpen: false
  }

  constructor(props: AppProps) {
    super(props);

    // properties
  }

  private _modalInterval: any = null;

  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];
  
    const urlParams = new URLSearchParams(window.location.search);
    let refAddress = urlParams.get('ref');
    if(refAddress != null){
      window.refferalAddress = refAddress;
    }    

    this._modalInterval = setInterval(this.handlePhase2Modal, 2000);
  }

  componentWillUnmount = () => { 
    
  }

  handlePhase2Modal = () => {
    let countDownDateUTC = new Date();
    countDownDateUTC.setUTCDate(15);
    countDownDateUTC.setUTCMonth(10);
    countDownDateUTC.setUTCFullYear(2021);
    countDownDateUTC.setUTCHours(22);
    countDownDateUTC.setUTCMinutes(0);
    countDownDateUTC.setUTCSeconds(0);

    if((new Date()).getTime() < countDownDateUTC.getTime()){
      this.setModalOpen();
    }else{
      if(this._modalInterval != null ){
        clearInterval(this._modalInterval);
        this._modalInterval = null;
      }
    }
  }

  setModalOpen = () => {
    this.setState({modalOpen: true});
  }

  setModalClose = () => {
    clearInterval(this._modalInterval);
    this._modalInterval = null;
    this.setState({modalOpen: false});
  }

  copyRefToClipboard = () => {
    let refAddress = window.location.origin + "?ref=" + window.walletAddress;
    copy(refAddress);
  }

  //  Main Layout
  //
  render = () => {
    return (
      <Box sx={{ flexGrow: 1 }} style={{width: "80%", margin: "auto", marginTop: "32px"}}>
        <Grid id="mainApp" container spacing={2}>
          <Grid item xs={12}>
            {/* <Item style={{textAlign: "left"}}><img style={{width: "100%"}} src={headerbg}/></Item> */}
            <Item sx={{ display: { xs: 'block', md: 'none' } }} className={"flex-middle-center"}>
              <div className={"flex-middle-center"}>
                <img style={{width: "100%"}} src={bnbfarmtext}/>
              </div>
            </Item>
            <Item sx={{ display: { xs: 'none', md: 'block' } }}>
              <div className={"flex-middle-center"}>
                <img style={{width: "10%"}} src={bnbfarm}/>
                <img style={{width: "80%"}} src={bnbfarmtext}/>
              </div>
            </Item>
          </Grid>
          <Grid item xs={12} md={4}>
            <Paper style={{padding: "16px"}}>
              <ConnectButton />

              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                referral link
              </Typography>
              <Typography sx={{ fontSize: 16 }} style={{wordBreak: "break-all"}} color="text.primary" gutterBottom>
                {window.location.origin}?ref=<span id={"walletAddressRefLink"}></span><br/>
                <Button color={"secondary"} variant={"contained"} sx={{fontSize: 12}} onClick={this.copyRefToClipboard}>copy</Button>              
              </Typography>
              <Typography sx={{ fontSize: 12 }} color="text.secondary" gutterBottom>
                For the friends that you invite you receive 5% of their deposits.
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={4}>
            <ContractStats></ContractStats>
          </Grid>
          <Grid item xs={12} md={4}>
            <UserStats></UserStats>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"Basic 1"} description={"Invest in this plan to activate the VIP plans"} minInvest={0.05} percentage={7} days={30} planIndex={1}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"Basic 2"} description={"Invest in this plan to activate the VIP plans"} minInvest={0.05} percentage={5} days={50}  planIndex={2}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"VIP 1 HUSTLER"} description={"Enable this plan by investing at least 1 BNB in 1 or 2"} minInvest={1.5} percentage={23} days={7} planIndex={0}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"VIP 2 BALLER"} description={"Enable this plan by investing at least 1 BNB in 1 and 2"} minInvest={3} percentage={32} days={5}  planIndex={3}></Plan>
          </Grid>
          {/* <Grid item xs={12} md={12}>
            <PlanDynamic label={"Dynamic"} percentage={7} days={20}></PlanDynamic>
          </Grid> */}

          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Select your plan and Invest, then withdraw earnings at least every 72 hours. The principal deposit cannot be withdrawn, only the retuns and referral rewards are paid out. Check the Transactions on Contract and Contract Source. High Risk and High Rewards! DYOR!
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                A 10% deposit fee is paid to the project/marketing wallet but this fee is not paid by you as a user, this fee is paid by the contract as a whole. your investment will get to the plan deposits 1:1. <br/>
                A 5% withdraw/reinvest fee is payed to the project/marketing wallet. this fee is also paid by the contract as a whole. <br/>
                20% of every withdraw will stay in the contract to allow longevity. this is not a tax that is used for marketing. it simply avoids the fast drain of the contract and solidifies the pool for withdrawls of all investors.<br/>
                <b>the displayed values on mybnbfarm are after tax. you will get what you see. the ROI value of each Plan is accurate.</b><br/>
                We are using this fees to pay marketing, developers and all other needs of this project.<br/>
              </Typography>
            </Paper>
          </Grid>
        </Grid>
        

        <Modal
          open={this.state.modalOpen}
          onClose={this.setModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
            🔔 ANNOUNCEMENT 🔔
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2, fontSize: 14 }}>
✏️ we are doing some changes on the investment plans.<br/><br/>

Why:<br/>
we got an surprisingly overwhelming bullrun on the VIP plan. To prevent the contract from drying out, we have to do some adjustments to keep the contract balance healthy. This will ensure the strength, longevity and success of the farm. 🚀📈<br/>
<br/>
Changes:<br/>
✅ Plan 1 will be exchanged with a new VIP plan<br/>
✅ the two VIP plans get new requirements to invest<br/>
✅ all plans get renamed<br/><br/>

FAQ:<br/>
❓When will the changes take place?<br/>
❕On Nov. 15 at 10pm UTC<br/><br/>

❓What does that mean to my current investments?<br/>
❕Nothing on your current rewards is changed<br/><br/>

❓Do I have to take any actions?<br/>
❕No you don't<br/><br/>

❓More questions?<br/>
❕Just ask in the group or per DM one of the Mods/Admins
            </Typography>
            <Button sx={{my: 1}} size="small" fullWidth={true} variant={"contained"} onClick={this.setModalClose}>close</Button>
          </Box>
        </Modal>
      </Box>
    );
  }
}

export default App;
